@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,800,900);
body {
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, left top, right top, from(rgba(23,23,23,1)), to(rgba(79,79,79,1)));
  background: -webkit-linear-gradient(left, rgba(23,23,23,1) 0%, rgba(79,79,79,1) 100%);
  background: linear-gradient(90deg, rgba(23,23,23,1) 0%, rgba(79,79,79,1) 100%);
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.montserat {
  font-family: 'Montserrat', sans-serif;
}
.particles {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: -1;
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
